import { FilePickerVue as FilePicker } from '@nextcloud/dialogs/filepicker.js';
import { translate as t } from '@nextcloud/l10n';
import { generateUrl } from '@nextcloud/router';
import { defineComponent } from 'vue';
export default defineComponent({
  name: 'FileReferencePickerElement',
  components: {
    FilePicker
  },
  props: {
    providerId: {
      type: String,
      required: true
    },
    accessible: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    containerId() {
      return `filepicker-${Math.random().toString(36).slice(7)}`;
    },
    filepickerOptions() {
      return {
        allowPickDirectory: false,
        buttons: this.buttonFactory,
        container: `#${this.containerId}`,
        multiselect: false,
        name: t('files', 'Select file or folder to link to')
      };
    }
  },
  methods: {
    t,
    buttonFactory(selected) {
      const buttons = [];
      if (selected.length === 0) {
        buttons.push({
          label: t('files', 'Choose file'),
          type: 'tertiary',
          callback: this.onClose
        });
      } else {
        buttons.push({
          label: t('files', 'Choose {file}', {
            file: selected[0].basename
          }),
          type: 'primary',
          callback: this.onClose
        });
      }
      return buttons;
    },
    onClose(nodes) {
      if (nodes === undefined || nodes.length === 0) {
        this.$emit('cancel');
      } else {
        this.onSubmit(nodes[0]);
      }
    },
    onSubmit(node) {
      const url = new URL(window.location.href);
      url.pathname = generateUrl('/f/{fileId}', {
        fileId: node.fileid
      });
      url.search = '';
      this.$emit('submit', url.href);
    }
  }
});